h6,
.heading-style-h6, h5,
.heading-style-h5, h4,
.heading-style-h4, h3,
.heading-style-h3, h2,
.heading-style-h2, h1,
.heading-style-h1 {
  margin-top: 0;
  margin-bottom: 0;
  font-family: "FT Sterling", Verdana, sans-serif;
  font-weight: 600;
}

.text-size-tiny, .text-size-small, .text-size-regular, .text-size-medium, .text-size-large, .text-size-xlarge, body {
  margin: 0;
  font-family: "Mona Sans", Verdana, sans-serif;
  font-weight: 400;
}

body * {
  color: inherit;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

section,
.section {
  position: relative;
  overflow: hidden;
}

a {
  color: inherit;
  text-decoration: inherit;
  font-size: inherit;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

.container-small,
.container-medium,
.container-large {
  margin-right: auto !important;
  margin-left: auto !important;
}

body {
  font-size: 1.125rem;
  line-height: 1.4em;
  letter-spacing: 0em;
}

h1,
.heading-style-h1 {
  font-size: 4.75rem;
  line-height: 1.2em;
  letter-spacing: 0.01em;
}
@media only screen and (max-width: 767px) {
  h1,
  .heading-style-h1 {
    font-size: 3.75rem;
  }
}

h2,
.heading-style-h2 {
  font-size: 4rem;
  line-height: 1.4em;
  letter-spacing: 0em;
}
@media only screen and (max-width: 767px) {
